@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&family=Parisienne&display=swap');
.App {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  margin-right: auto;
  margin-left: auto;
  padding-left: 20px;
  padding-right: 20px;
  max-width: 1300px;

}
body{
  background-color: #E1DBE2;
}

nav{
 margin-top: 20px;
  height: 60px;
  text-align: end;
  align-content: center;
  margin-right: 80px;
}
a.nav-item{
  display: inline-block;
  width:120px;
  font-size: 18px;
  text-decoration: none;
  font-family: Arial, sans-serif;
  color:black;
}
.welcome{
  font-family: 'Times New Roman', Times, serif;
  font-size: 130px;
  font-weight: lighter;
  margin: 0px;
  margin-top: 180px;;
}
.portfolio{
  font-family: 'Parisienne', cursive;
  font-weight: 400;
  font-style: normal;
  font-size: 220px;
  margin: 0px;
}
.footer-section{
  display: inline-block;
  font-family: Arial, sans-serif;
  font-weight: lighter;
  font-size: 18px;
  margin: 100px;
  margin-bottom: 30px;

}
.footer{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: end;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100px;
  background-color: white;
}
.about-section{
  width: 100%;
  height: 100%;
  text-align: start;
  margin-left: 80px;
  margin-right: 80px;
  font-family: Arial, sans-serif;
  font-weight: lighter;
  font-size: 18px;
  text-decoration: none;
  color: black;
}
.skills-container{
  text-align: start;
  margin-bottom: 10px;
  font-size: 10px;
}
.skills{
  padding:  3px 20px 3px 20px;
  display: inline;
  margin: 10px;
  font-size: 20px;
  text-decoration: none;
  color: black;
  
}
.js{
  background-color: #D9D2C9;
  border-radius: 25px;
}
.html{
  border: 1px black solid;
  border-radius: 25px;
}
.about-text{
  width: 40%;
  font-size: 18px;
  text-decoration: none;
  color: black;
  font-family: Arial, sans-serif;
  font-weight: lighter;
  margin-bottom: 50px;
}
.home-section{
  text-align: center;
}
.projects-text{
  font-family: Arial, sans-serif;
  font-weight: lighter;
  font-size: 18px;
  text-decoration: none;
  color: black;
}
.about{
  text-align: start;
  text-decoration: none;
  color: black;
  font-weight: 400;
  
}
.ellipse{
  display: block;
  margin-left: 70%;
  margin-bottom: 0px;
}

.buttonBack___1mlaL{
  background-color: none;
  height: 5vw;
  width: 5vw;
  min-width: 80px;
  font-size: 18px;
  border:1px solid;
  border-color:#E2482C ;
  border-radius: 20px;
  color: #E2482C;
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-style: normal;
  margin: 10px;
}
.buttonNext___2mOCa{
  background-color: #F89B84;
  height: 5vw;
  width: 5vw;
  min-width: 80px;
  font-size: 18px;
  border:0;
  border-radius: 20px;
  color: #E2482C;
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-style: normal;
  margin: 10px;
}
.buttons{
  display: flex;
  flex-direction: column;
  margin-top: auto;
  border-radius: 25px;
  border:1;
  margin-left: 10px;
  padding-top: 10vw;
}
.container{
  border-radius: 25px;
  display:flex;
  flex-direction: row;
}
.slider{
 width:100vw;
}
.project-container{
  margin: 10px;
  
}
.project-container-slider{
  background-color: #E3B98B;
  border-radius: 20px;
  margin-bottom: 20px!important;
}
.project-container-slider div{
  padding-left: 2vw;
  text-align: start;
}
.project-container-text{
  font-size: 70px;
  text-decoration: none;
  color: #E4462E;
  font-family: Arial, sans-serif;
  font-weight: bold;
}
.project-text{
  font-size: 18px;
  font-family: Arial, sans-serif;
  text-decoration: none;
  color: black;
  font-weight: lighter;
}
.about-section{
  margin-bottom: 150px;
}
.contact{
  text-align: start;
}
@media screen and (max-width:900px){

  .buttonBack___1mlaL{
    font-size: 14px;
  }
  .buttonNext___2mOCa{
    font-size: 14px;
  }
  .project-text{
    font-size: 0px;
  }
  nav{
    margin-right: 0px;
    text-align: center;
  }
  a.nav-item{
    font-size: 14px;
    width:80px;
  }
  .footer-section{
    font-size: 14px;
    margin: 20px;
    margin-bottom: 10px;
  }
  .body{
    margin-top: 10vw;
  }
  .container{
    display: block;
  }
  .slider{
    width: 90vw;
  }
  .buttons{
    margin-top: 0px;
    border-radius: 25px;
    background-color: none!important;
    border:1;
    padding-top: 0vw;
    align-items: flex-end;
  }
  .project-container-text{
    font-size: 35px;
    margin-bottom: 10px;
  }
  .welcome{
    font-size: 40px;
  }
  .portfolio{
    font-size: 80px;
  }
}